<template>
  <div id="banner">
    <div class="BackgSwitc" style="height: 100%;">
      <video id="video" width="100%" autoplay loop="loop" requestFullscreen>
        <!-- <source src="commons/images/logobg.ogg" type="video/ogg"> commons/images/logobg.mp4 -->
        <source src type="video/mp4">
      </video>
    </div>
    <div class="box">
      <div id="login-navigation" class="type-area">
        <div class="logo clearfix">
          <img src alt style="width: auto;height: 32px">
          <!-- <h1 class="logo-titel"></h1> -->
          <span class="logo-grade"></span>
        </div>
        <ul class="container clearfix">
          <li>
            <a href="javascript:;" target="_blank"></a>
          </li>
          <li>
            <a href="javascript:;" target="_blank"></a>
          </li>
          <li>
            <a href="javascript:;" target="_blank"></a>
          </li>
          <li>
            <a href="javascript:;" target="_blank"></a>
          </li>
        </ul>
      </div>
      <div id="login-main" class="type-area">
        <div class="banner-text">
          <!--  -->
          <div class="text">
            <ul>
              <li>
                <div class="big-text">
                  <span class="Chinese"></span>
                  <span class="English"></span>
                </div>
                <p class="small-text"></p>
              </li>
            </ul>
          </div>
        </div>
        <div class="container-logintext">
          <div class="login-form">
            <div class="form-title">
              <ul id="tab-account" class="tab-account">
                <li class="current">
                  <a class="active userLogin" href="#" title="login-account"></a>
                </li>
                <li>
                  <a href="#" title="login-scan-code" class="codeLogin"></a>
                </li>
              </ul>
              <p id="tab-phone" style="display: none">
                <a class="loginPhone" href="javascript:;"></a>
              </p>
            </div>
            <div id="fm1" name="fm1" action method>
              <div id="error-message" class="eer">
                &nbsp;
                <!-- <div style="text-align: left;padding-left: 25px;color: rgba(255, 0, 0, 0.7);" th:if="${param.error}">
                                
                            </div>
                            <div style="text-align: left;padding-left: 25px;color: rgba(255, 0, 0, 0.7);" th:if="${param.logout}">
                                
                </div>-->
              </div>
              <div id="login-account" class="item show">
                <form id="formid" class="formid" :action="login_form_url" method="post">
                  <div class="input-k">
                    <i class="iconfont icon-touxiang"></i>
                    <i class="line"></i>
                    <input type="text" placeholder="" id="username" name="username">
                  </div>
                  <div class="input-k">
                    <label>
                      <i class="iconfont icon-suo"></i>
                      <i class="line"></i>
                      <input type="password" placeholder="" id="userpwd" name="password">
                    </label>
                  </div>
                  <div class="input-k" style="display: none" id="login_code_img_container">
                    <label>
                      <input
                        type="text"
                        placeholder=""
                        id="verifcation_code_2"
                        style="width: 115px;"
                      >
                      <img src id="code_img_2">
                      <img :src="Base.getOauthHost() + '/commons/images/code_refresh.png'" alt id="code_refresh_2">
                    </label>
                  </div>
                  <div class="qita-g">
                    <input class="login-submit" value="" type="button" @click="loginSubmit">
                    <input>
                  </div>
                </form>
                <!-- <div class="input-g">
                                  <i class="iconfont icon-touxiang">
                                  </i>
                                  <i class="line"></i>
                                  <input id="username" name="username" placeholder="" />
                              </div>
                              <div class="input-g">
                                  <i class="iconfont icon-suo"></i>
                                  <i class="line"></i>
                                  <input id="password" name="password" placeholder="" type="password" />
                              </div>
                              <div class="qita-g">
                                  <input type="button" class="login-submit" id="login-submit" style="font-size: 15px" value=" " />
                </div>-->
                <div class="logon-type">
                  <a class="phone" href="javascript:;" data-type="phone"></a>
                  <!-- <a href="change-password.html"></a> -->
                </div>
              </div>
              <!--  -->
              <div id="login-scan-code" class="item" style="position: relative;">
                <!--<img src="/commons/images/jqerma.jpg" alt="">-->
                <img src alt>
                <p></p>
                <div
                  class="code_back"
                  style="display:none;width: 76%;height: 100%;position: absolute;left: 34px;top: 0;background: rgba(0, 0, 0, 0.50);"
                >
                  <p
                    style="margin-top: 50px;color: #fff;font-size: 22px;font-family: '';font-weight: 500;"
                  ></p>
                  <span
                    click
                    style="display: inline-block;color: #fff;background: #1f95ff; padding: 7px 35px;margin-top: 30px; border-radius: 3px;letter-spacing: 5px;font-size: 16px"
                  ></span>
                </div>
              </div>
              <!--  -->
              <form id="phone_formid" class="formid" th:action="@{/login}" method="post">
                <div id="login-phone" class style="display: none">
                  <div class="input-k">
                    <i class="iconfont icon-shouji"></i>
                    <i class="line"></i>
                    <input id="phone" name="username" type="text" placeholder="">
                  </div>
                  <div class="input-k">
                    <i class="iconfont icon-yanzhengma"></i>
                    <i class="line"></i>
                    <input id="code" name="password" placeholder="" type="text">
                    <span class="send-message"></span>
                    <span class="wait_code" style="display:none; color: #219afe; margin: 0 24px;"></span>
                  </div>
                  <div class="qita-g">
                    <input type="button" class="login-submit" id="phone-submit" value=" ">
                  </div>
                  <div class="logon-type qita-g">
                    <a class="goback" data-type="Account" href="#"></a>
                  </div>
                </div>
              </form>
              <div class="login-more">
                <div class="login-third">
                  <span class="left"></span>
                  
                  <span class="right"></span>
                </div>
                <div class="login-icon">
                  <div class="third-box">
                    <a class id="third-qq" href="javascript:;">
                      <img :src="Base.getOauthHost() + '/commons/images/weixin.png'" alt>
                    </a>
                    <a class id="third-weixin" href="javascript:;">
                      <img :src="Base.getOauthHost() + '/commons/images/qq.png'" alt>
                    </a>
                    <a class id="third-weibo" href="javascript:;">
                      <img :src="Base.getOauthHost() + '/commons/images/weibo.png'" alt>
                    </a>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
    <div class="verification-code-mask" id="mask">
      <div class="verification-code">
        <h5></h5>
        <div class="ipt-code-refresh">
          <div class="ipt-code">
            <input type="text" placeholder="" id="verifcation_code">
            <img src id="code_img">
          </div>
          <div class="code_refresh">
            <img :src="Base.getOauthHost() + '/commons/images/code_refresh.png'" alt>
          </div>
        </div>
        <p class="tip" id="code_tip"></p>
        <a href="javascript:;" class="btn" id="code_submit"> </a>
        <a href="javascript:;" class="close" id="code_close">
          <img :src="Base.getOauthHost() + '/commons/images/code_close.png'" alt>
        </a>
      </div>
    </div>
	<footer id="footer">
		<div class="content type-area">
			<cite></cite>
			<br>
			<span></span>
			<a href="#" target="_blank" style="color: #797979;"></a>
		</div>
	</footer>
  </div>
</template>

<script>
import Base from "@static/js/base.js";
import $ from "jquery";
import "@static/js/jQuery.md5.js";
import "@static/js/navoverall.js";

import CryptoJS from "@static/js/aes/core.js";
import "@static/js/aes/md5.js";
import "@static/js/aes/evpkdf.js";
import "@static/js/aes/enc-base64.js";
import "@static/js/aes/cipher-core.js";
import "@static/js/aes/aes.js";
import "@static/js/aes/hmac.js";
import "@static/js/aes/sha1.js";
import "@static/js/aes/sha256.js";

import "@static/css/iconfont.css";
import "@static/css/login.css";

import { mapState, mapGetters } from 'vuex'

export default {
  name: "HelloWorld",
  mounted() {
	  this.loginHader();
	  this.addEventListener();
  },
  computed: {
    ...mapState([
            "user"
        ]),
        ...mapGetters([
            "isLogin",
            "loginInfo"
        ])
  },
  data() {
    return {
      _detrat: null,
	  _time: null,
	  login_form_url: Base.getOauthHost() + "/login?service=" + encodeURIComponent("http://" + Base.getLocalHost() + "/index"),
	  Base: Base
    };
  },
  methods: {
    addEventListener: function() {
      var that = this;
      $(document).keyup(function(event) {
        if (event.keyCode == 13) {
          that.loginSubmit();
        }
      });
      $("#code_refresh_2,#code_img_2").click(function(e) {
        $("#code_img_2").attr(
          "src",
          Base.getOauthHost() + "/api/validateCode?t=" + new Date().getTime()
        );
      });
      $("#code_img").attr(
        "src",
        Base.getManageHost() +
          "/iapp/api/validateCode?t=" +
          new Date().getTime()
      );
      $(".formid").on("click", "input", function(e) {
        e.stopPropagation();
        $(this)
          .parents(".input-k")
          .addClass("actives")
          .siblings()
          .removeClass("actives");
      });
      $(document).on("click", function() {
        $(".input-k").removeClass("actives");
        $(".ipt-code").removeClass("code_actives");
      });
      $(".ipt-code input").click(function(e) {
        e.stopPropagation();
        $(".ipt-code").addClass("code_actives");
      });
      $("#login-submit").click(function() {
        that.loginSubmit();
      });
      //   $(document).keydown(function(event) {
      //     if (event.keyCode == 13) {
      //       that.loginSubmit();
      //     }
      //   });
      $(".logon-type a")
        .unbind("click")
        .click(function() {
          var type = $(this).attr("data-type");
          if (type == "phone") {
            $("#tab-account").hide();
            $("#tab-phone").show();
            $("#msg").hide();
            $("#login-account").hide();
            $("#login-phone").show();
            $("#error-message")
              .children()
              .remove();
            var phone = $("#phone").val();
            if (phone) {
              $.ajax({
                url:
                  Base.getOauthHost() + "/api/switch/operation?phone=" + phone,
                type: "GET",
                success: function(data) {}
              });
            }
          } else {
            $("#error-message")
              .children()
              .remove();
            $("#tab-account").show();
            $("#tab-phone").hide();
            $("#msg").hide();
            $("#login-account").show();
            $("#login-phone").hide();
          }
        });

      $("#phone-submit").click(function loginSubmit() {
        that.phoneSubject();
      });
      $(".code_refresh,#code_img").click(function(e) {
        $("#code_img").attr(
          "src",
          Base.getManageHost() +
            "/iapp/api/validateCode?t=" +
            new Date().getTime()
        );
      });

      $("#code_close").click(function() {
        $("#mask").hide();
      });
      $("#code_submit").click(function() {
        var phone = $("#phone").val();
        var code = $("#verifcation_code").val();
        if (!code) {
          return false;
        }
        $.ajax({
          url: Base.getManageHost() + "/iapp/api/verifyCode",
          type: "GET",
          dataType: "jsonp",
          jsonp: "codeCallback",
          jsonpCallback: "codeCallback",
          data: {
            vcode: code,
            callback: "codeCallback",
            phone: phone
          },
          success: function(data) {
            if (data && data.result) {
              $.ajax({
                url:
                  Base.getOauthHost() +
                  "/api/validationcode?type=phone&phone=" +
                  phone +
                  "&key=" +
                  data.key,
                type: "GET",
                success: function(data) {
                  that.codeRest();
                  $("#mask").hide();
                  $("#code_tip").hide();
                  if (data.code == "SUCCESS") {
                    that._time = 90;
                    $(".wait_code").html(that._time + "s");
                    that.showcode(true);
                    setTimeout(showcode, that._time * 1000);
                  } else {
                    $("#msg").hide();
                    $("#error-message").html(
                      '<div id="msg" class="errors">' +
                        data.message +
                        "</div>"
                    );
                    $("#error-message").show();
                  }
                },
                error: function(obj) {
                  that.codeRest();
                  $("#mask").hide();
                  $("#code_tip").hide();
                  $("#msg").hide();
                  $("#error-message").html(
                    '<div id="msg" class="errors">' + obj + "</div>"
                  );
                  $("#error-message").show();
                }
              });
            } else {
              $("#code_tip").html("");
              $("#code_tip").show();
            }
          },
          error: function(obj) {
            $("#msg").hide();
            $("#error-message").html(
              '<div id="msg" class="errors"></div>'
            );
            $("#error-message").show();
          }
        });
      });

      $(".send-message")
        .unbind("click")
        .click(function() {
          $("#msg").hide();
          $("#error-message").html("");
          $("#msg").hide();
          var phone = $("#phone").val();
          if (that.isModilePhone(phone)) {
            $("#mask").show();
          } else {
            $("#msg").hide();
            $("#error-message").html(
              '<div id="msg" class="errors"></div>'
            );
            $("#error-message").show();
          }
        });
      $("#tab-account a").click(function(e) {
        e.preventDefault();
        var isScanCode =
          $(this).attr("title") == "login-scan-code" &&
          !$(this).hasClass("current");
        $("#tab-account li a")
          .removeClass("current")
          .removeClass("active");
        $(this)
          .addClass("current")
          .addClass("active");
        $(".item")
          .removeClass("show")
          .hide();
        $("#" + $(this).attr("title"))
          .addClass("show")
          .show();
        $("#error-message")
          .children()
          .remove();
        //  
        if (isScanCode) {
          $(".code_back").hide();
          that.scanCode();
        }
      });
      $(".code_back")
        .unbind("click")
        .click(function() {
          $(".code_back").hide();
          that.scanCode();
        });
    },
    loginHader: function() {
      $.ajax({
        url: Base.getOpenHost() + "/api/settings",
        type: "GET",
        success: function(data) {
          document.title = data[2].value + "_" + data[5].value;
          var titleName = "",
            titleIntroduce = "";
          if (data && data.length > 0) {
            $.each(data, function(i, v) {
              if (v.key == "commons.WEBSITE_HOME_LOGO") {
                $(".logo img").attr(
                  "src",
                  Base.getOpenHost() + "/api/service/image/" + v.value
                );
              }
              if (v.key == "commons.WEBSITE_BACKGR_IMG" && v.name == "Img") {
                var tpl =
                  '<img src="' +
                  Base.getOpenHost() +
                  "/api/service/file/" +
                  v.value +
                  '" style="width: 100%;height:100%;">';
                $(".BackgSwitc").html(tpl);
              }
              if (v.key == "commons.WEBSITE_BACKGR_IMG" && v.name == "Vid") {
                var tpl =
                  '<video id="video" width="100%" height="auto" autoplay loop="loop" requestFullscreen><source src="' +
                  Base.getOpenHost() +
                  "/api/service/file/" +
                  v.value +
                  '" type=video/mp4"><source src="' +
                  Base.getOpenHost() +
                  "/api/service/file/" +
                  v.value +
                  '" type="video/ogg">Video</video>';
                $(".BackgSwitc").html(tpl);
              }
              if (v.key == "commons.WEBSITE_TITLE_INTRODUCE") {
                $(".logo .logo-grade").html(v.value);
              }
              if (v.key == "commons.SCHOOL_WEBSITE") {
                $(".container li:eq(0) a").attr("href", v.value);
              }
              if (v.key == "commons.APP_CENTER") {
                $(".container li:eq(1) a").attr("href", v.value);
              }
              if (v.key == "commons.DATA_BRAIN") {
                $(".container li:eq(2) a").attr("href", v.value);
              }
              if (v.key == "commons.OPEN_PLATFORM") {
                $(".container li:eq(3) a").attr("href", v.value);
              }
              if (v.key == "commons.SLOGAN_Field_1") {
                $(".text ul li .Chinese").html(v.value);
              }
              if (v.key == "commons.SLOGAN_Field_2") {
                $(".small-text").html(v.value);
              }
              if (v.key == "commons.SLOGAN_Field_2") {
                $(".small-text").html(v.value);
              }

              if (v.key == "commons.WEBSITE_COPYRIGHT") {
                $("#footer .type-area cite").html(v.value);
              }
              if (v.key == "commons.WEBSITE_TECHNICAL_SUPPORT") {
                $("#footer .type-area a").html(v.value);
              }
              if (v.key == "commons.WEBSITE_TECHNICAL_SUPPORT_LINK") {
                $("#footer .type-area a").attr("href", v.value);
              }
              if (v.key == "commons.WEBSITE_TITLE_NAME") {
                titleName = v.value;
              }
              if (v.key == "commons.WEBSITE_TITLE_INTRODUCE") {
                titleIntroduce = v.value;
              }
            });
            var storage = window.localStorage;
            storage.setItem("Title", titleName + "_" + titleIntroduce);
          }
        },
        error: function(obj) {}
      });
    },
    scanSuccess: function(code) {
      var that = this;
      $.post(
        Base.getOauthHost() + "/connect/auth",
        {
          token: code
        },
        function(data) {
          if (data.code == 200) {
            $("#username").val(data.data.username);
            $("#userpwd").val(data.data.userpwd);
            $("form#formid").submit();
          } else {
            that.scanSuccess(code);
          }
        }
      );
    },
    inspectPool: function(code) {
      var that = this;
      $.post(
        Base.getOauthHost() + "/connect/inspect",
        {
          token: code
        },
        function(data) {
          if (data.code == 200) {
            $("#login-scan-code p").html(data.msg);
            scanSuccess(data.data.token);
          } else if (data.code == 201) {
            $("#login-scan-code p").html("");
            $(".code_back").show();
            $(".code_back p").html(data.msg);
          } else {
            that.inspectPool(code);
          }
        }
      );
    },
    scanCode: function() {
		let that = this;
		$.ajax({
			type: "GET",
			url: Base.getOauthHost() + "/connect/code",
			success: function(data) {
				if (data != undefined) {
					var code = data;
					$.ajax({
						type: "GET",
						url: Base.getOauthHost() + "/connect/qrcode/" + data,
						success: function(data) {
							$("#login-scan-code img").attr("src",Base.getOauthHost() + "/connect/qrcode/" + code);
							$("#login-scan-code p").html("App");
							setTimeout(function() {
								that.inspectPool(code);
							}, 100);
						},
						error: function(e) {
							$("#msg").hide();
							$("#error-message").html(
							'<div id="msg" class="errors">' + obj + "</div>"
							);
							$("#error-message").show();
						}
					});
				}
			},
			error: function(obj) {
			$("#msg").hide();
			$("#error-message").html(
				'<div id="msg" class="errors">' + obj + "</div>"
			);
			$("#error-message").show();
			}
		});
    },
    codeRest: function() {
      $("#code_img").attr(
        "src",
        Base.getManageHost() +
          +"/iapp/api/validateCode?t=" +
          new Date().getTime()
      );
      $("#verifcation_code").val("");
    },
    phoneSubject: function() {
      $("#msg").hide();
      $("#error-message").html(" ");
      $("#error-message").hide();
      var phone = $("#phone").val();
      var code = $("#code").val();

      if (!phone) {
        $("#msg").hide();
        $("#error-message").show();
        $("#error-message").html(
          '<div id="msg" class="errors"></div>'
        );
        return;
      }

      if (!this.isModilePhone(phone)) {
        $("#msg").hide();
        $("#error-message").show();
        $("#error-message").html(
          '<div id="msg" class="errors"></div>'
        );
        return;
      }

      if (!code) {
        $("#msg").hide();
        $("#error-message").show();
        $("#error-message").html(
          '<div id="msg" class="errors"></div>'
        );
        return;
      }

      var form = document.getElementById("phone_formid");
      //input
      $.ajax({
        url:
          Base.getOauthHost() +
          "/api/verify/code?phone=" +
          phone +
          "&code=" +
          code,
        type: "GET",
        success: function(data) {
          if (data.code == "401") {
            $("#error-message")
              .html('<div id="msg" class="errors">' + data.msg + "</div>")
              .show();
          } else {
            form.submit();
          }
        }
      });
    },
    isModilePhone: function(v) {
      var regex = /^(13|15|18|17)[0-9]{9}$/;
      return regex.test(v);
    },
    showcode: function(v) {
      if (v) {
        $(".send-message").css({ display: "none" });
        $(".wait_code").css({ display: "inline-block" });
        this._detrat = setInterval(this.detract(), 1000);
      } else {
        $(".send-message").css({ display: "inline-block" });
        $(".wait_code").css({ display: "none" });
        clearInterval(this._detrat);
      }
    },
    settime: function() {
      var count = this._time;
      var timer = null;
      timer = setInterval(function() {
        if (count > 0) {
          count = count - 1;
          $(".wait_code").html(count + "s");
        } else {
          clearInterval(timer);
          $(".send-message").html("");
        }
      }, 1000);
    },
    detract: function() {
      $(".wait_code").html(this._time + "s");
      this.settime(this._time);
    },
    loginSubmit: function() {
		let that = this;
      var username = $("#username").val(),
        userpwd = $("#userpwd").val(),
        code = $("#verifcation_code_2").val();
      if (!username && !userpwd) {
        $("#error-message")
          .html('<div id="msg"></div>')
          .show();
        return;
      }
      if (!username) {
        $("#error-message")
          .html('<div id="msg"></div>')
          .show();
        return;
      }
      if (!userpwd) {
        $("#error-message")
          .html('<div id="msg"></div>')
          .show();
        return;
      }
      if (window.verifcationCode && !code) {
        $("#error-message")
          .html('<div id="msg"></div>')
          .show();
        return;
      }
      $.ajax({
        type: "post",
        url: Base.getOauthHost() + "/api/verify",
        dataType: "json",
        data: {
          username: $.trim(username),
          //"password": $.md5($("#password").val())
          userpwd: $.trim(userpwd),
          verifcationCode: window.verifcationCode ? $.trim(code) : ""
        },
        success: function(result) {
          if (result.code == 0) {
			//$("form#formid").submit();
			
            // var token = result.data.access_token;
