			var Url = Base.getAppHost() + '/api/mobile?grant_type=password&username='+ $.trim(username)
                + '&password=' + pwd
				+ '&scope=all';
			that.axios({
				method: 'post',
				url: '/iapp/api/mobile?grant_type=password&username=' + $.trim(username) + '&password=' + pwd + '&scope=all',
				data: {
                    "username": $.trim(username),
                    "password": pwd
                }
			}).then((response) => {
				let data = response.data;
				if (data.code == '200') {
					var access_token = data.data.access_token;
					that.axios.get("/iapp/appLogin?ticket="+access_token+'&useragent=weixin').then((data2) => {
						that.axios.get("/news?useragent=ysjy_web&ticket=" + access_token).then((data3)=>{
							that.axios.get("/iapp/whoami").then((data4) => {
								if(data4.data && data4.data.account) {
									that.$store.state.user = data4.data;
									sessionStorage.setItem('localusr', JSON.stringify(data4.data));
									that.$router.replace('index');
								} else {
									$("#msg").hide();
									$("#error-message").html('<div id="msg">,</div>');
									$("#error-message").show();
								}
							})
						});
					});
				} else {
					console.log(data);
					return;
				}
			});
