          } else {
            if (result.code == 100) {
              window.verifcationCode = true;
              $("#code_img_2").attr(
                "src",
                Base.getOauthHost() +
                  "/api/validateCode?t=" +
                  new Date().getTime()
              );
              $("#login_code_img_container").show();
            }
            $("#msg").hide();
            $("#error-message")
              .html('<div id="msg">' + result.msg + "</div>")
              .show();
            var code = result.code;
            if (code == 302) {
              localStorage.setItem("username", $.trim(username));
              window.location.href = Base.getOauthHost() + "/reset_password";
            }
            $("#error-message").show();
            return;
          }
        },
        error: function() {
          $("#msg").hide();
          $("#error-message").html('<div id="msg">,</div>');
          $("#error-message").show();
          return;
        }
      });
	},
	encrypt: function (word) {
		var me = this;
			me.key = CryptoJS.enc.Utf8.parse("c2A89L6DkTAFysjy");  //
			me.iv = CryptoJS.enc.Utf8.parse('dMitHORyqbeYVE0o');
		var srcs = CryptoJS.enc.Utf8.parse(word);
		var encrypted = CryptoJS.AES.encrypt(srcs, me.key, { iv: me.iv, mode: CryptoJS.mode.CBC, padding: CryptoJS.pad.Pkcs7 });
		return encrypted.toString();
	}
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.verification-code {
  width: 269px;
  height: 200px;
  margin: 0 auto;
  background: #fff;
  text-align: center;
  border-radius: 5px;
  position: absolute;
  left: 780px;
  top: 284px;
  right: 0;
}
html,
body,
.verification-code-mask {
  height: 100%;
}
.verification-code-mask {
  background: #04040480;
  filter: alpha(opacity=151);
  display: none;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
}
h5 {
  margin: 18px auto;
  font-size: 16px;
  color: #333333;
}
.ipt-code-refresh {
  overflow: hidden;
  padding: 0 21px;
  margin-top: 25px;
}
.ipt-code:hover {
  border: 1px solid #1f95ff;
}
.ipt-code {
  float: left;
  width: 83%;
  height: 40px;
  overflow: hidden;
  border: 1px solid #a5a5a5;
  border-radius: 25px;
  position: relative;
}
.code_actives {
  border: 1px solid #1f95ff;
}
.ipt-code input {
  outline: none;
  border: navajowhite;
  width: 100%;
  line-height: 40px;
  text-indent: 23px;
  background: #fff;
}
.ipt-code img {
  position: absolute;
  display: block;
  background: #999;
  width: 90px;
  height: 100%;
  right: 0;
  top: 0;
}
.refresh,
.code_refresh {
  line-height: 40px;
  cursor: pointer;
}
.code_refresh img {
  margin-left: 10px;
}
.close {
  position: absolute;
  right: -22px;
  top: -27px;
  display: block;
  width: 20px;
  height: 19px;
  text-align: center;
}
.btn {
  width: 82%;
  display: block;
  padding: 13px 0;
  color: #fff;
  background: #1f95ff;
  margin: 25px auto;
  border-radius: 25px;
  font-size: 16px;
}

.tip {
  color: red;
  text-align: left;
  padding: 0 28px;
  margin-top: 10px;
  display: none;
  position: absolute;
  top: 32px;
}
#verifcation_code::-moz-placeholder {
  color: #bababa;
}
#verifcation_code::-webkit-input-placeholder {
  color: #bababa;
}
#verifcation_code::-ms-input-placeholder {
  color: #bababa;
}
</style>



