<template>
    <div>
        <h1>Login Success!!!</h1>
        <div>
            loginInfo:<span>{{loginInfo}}</span>
        </div>
        <button @click="loginOut">login out</button>
    </div>
</template>
<script>
import Base from "@static/js/base.js";
import { mapState, mapGetters } from 'vuex'

export default {
    name: "Index",
    computed: {
        ...mapState([
            "user"
        ]),
        ...mapGetters([
            "isLogin",
            "loginInfo"
        ])
    },
    methods: {
        loginOut: function() {
            debugger
            $.get(Base.getManageHost() + "/commons/logout",null,function() {
                $.get(Base.getManageHost() + "/logout");
            });
            this.$store.state.user = null;
            sessionStorage.setItem('localusr', null);
            this.$router.replace('/');
        }
    }
}
</script>



