export default {
  APPOINT_LIST (state, data) {
    state.pendingList = data
    debugger
    if(!state.past) {
      state.numbers = data.numberOfElements
    }
  },
  RESERVATION_REVIEW_LIST (state, data) {
    state.pendingList = data
    if(!state.past) {
      state.number = data.numberOfElements
    }
  }
}



