import Vue from 'vue'
import axios from 'axios'
export default {
  selectedschool ({commit}) {
    axios.get(process.env.APP_HOST+'/api/selectedschool')
    .then(res => {
        console.log(res)
        // console.log(JSON.parse(res))
        Vue.selectedSchool = res.data
      })
  },
  appointList ({commit}, params) {
    axios.get(process.env.APP_HOST+'/api/app/boardroom/appoint/list', params)
      .then(res => {
				commit('APPOINT_LIST', res.data)
      })
  },
  reservationReviewList ({commit}, params) {
    axios.get(process.env.APP_HOST+'/api/app/boardroom/verify/past/'+257, params)
      .then(res => {
        commit('RESERVATION_REVIEW_LIST', res.data)
      })
  },
  searChange ({commit}, params) {
    axios.get(process.env.APP_HOST+'/api/app/boardroom/detail', params)
      .then(res => {
        commit("SEARCHANGE", res.data)
      })
  }
}


