import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/components/HelloWorld'
import Index from '@/components/Index'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'HelloWorld',
      component: HelloWorld,
      // redirect: '/venueIndex',
    },
    {
      path: '/index',
      name: 'Index',
      component: Index
    },
    {
      path: '/venueIndex',
      name: 'venueIndex',
      component: () => import("@/components/venueReservation/")
    },
    {
      path: '/venuePriority',
      name: 'venuePriority',
      component: () => import("@/components/venueReservation/venuePriority")
    },
    {
      path: '/timePriority',
      name: 'timePriority',
      component: () => import("@/components/venueReservation/timePriority")
    },
    {
      path: '/submitReservation',
      name: 'submitReservation',
      component: () => import("@/components/venueReservation/submitReservation")
    },
    {
      path: '/VenueDetails',
      name: 'VenueDetails',
      component: () => import("@/components/venueReservation/public/VenueDetails")
    },
    {
      path: '/AppointmentDetails',
      name: 'AppointmentDetails',
      component: () => import("@/components/venueReservation/public/AppointmentDetails")
    },
    {
      path: '/ConditionalScreening',
      name: 'ConditionalScreening',
      component: () => import("@/components/venueReservation/public/ConditionalScreening")
    },
    
    
  ]
})



