import Vue from 'vue'
import App from './App'
import router from './router'
import store from './store'
import axios from 'axios'
import vueAxios from 'vue-axios'
import VueAwesomeSwiper from 'vue-awesome-swiper'
import 'swiper/dist/css/swiper.css'
import * as filters from './filters' 
import "@static/js/Toast/wu-ui.css"
import "@static/js/Toast/iconfont.css"
import Toast from "@static/js/Toast"
import fastclick from 'fastclick'
Object.keys(filters).forEach(key => {
  Vue.filter(key, filters[key])
})
Vue.config.productionTip = false
Vue.prototype.$Toast = Toast 
fastclick.attach(document.body)
Vue.use(vueAxios, axios, VueAwesomeSwiper)

function initJsBridge(readyCallback) {
  var u = navigator.userAgent;
  var isAndroid = u.indexOf('ys_app_android') > -1; //android
  var isiOS = u.indexOf('ys_app_ios') > -1; //ios
  // jsbridge
  function connectWebViewJavascriptBridge(callback) {
      if (isAndroid) {
          if (window.WebViewJavascriptBridge) {
              callback(WebViewJavascriptBridge)
          } else {
              document.addEventListener(
                  'WebViewJavascriptBridgeReady'
                  , function () {
                      callback(WebViewJavascriptBridge)
                  },
                  false
              );
          }
          return;
      }
      if (isiOS) {
          if (window.WebViewJavascriptBridge) {
              return callback(WebViewJavascriptBridge);
          }
          if (window.WVJBCallbacks) {
              return window.WVJBCallbacks.push(callback);
          }
          window.WVJBCallbacks = [callback];
          var WVJBIframe = document.createElement('iframe');
          WVJBIframe.style.display = 'none';
          WVJBIframe.src = 'https://__bridge_loaded__';
          document.documentElement.appendChild(WVJBIframe);
          setTimeout(function () {
              document.documentElement.removeChild(WVJBIframe)
          }, 0)
      }
  }
  // 
  connectWebViewJavascriptBridge(function (bridge) {
      if (isAndroid) {
          bridge.init(function (message, responseCallback) {
              console.log('JS got a message', message);
              responseCallback(data);
          });
      }
      // callHandler
      bridge.registerHandler('jsbridge_showMessage', function (data, responseCallback) {
          showResponse(data);
      });
      bridge.registerHandler('jsbridge_getJsMessage', function (data, responseCallback) {
          showResponse(data);
          responseCallback('native ' + data);
      });
      readyCallback();
  });
}

function initVue() {
  new Vue({
    el: '#app',
    router,
    store,
    components: { App },
    template: '<App/>'
  })
}
var userAgent = window.navigator.userAgent;
if(userAgent.indexOf("ys_app")!=-1) {
    initJsBridge(initVue);
} else {
    initVue();
}


