const getters =  {
  isLogin: state => state.user ? true : false,
  loginInfo: state => JSON.stringify(state.user),
  pendingList: (state) => state.pendingList ? state.pendingList : null,
  numbers: state => state.numbers ? state.numbers : 0,
  past: state => state.past ? true : false,
}
export default getters


